import React, { useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import TitleData from "../data/TitleData";

import Reference from "./Reference";
import ReferenceData from "../data/ReferenceData";



export default function Courses() {
  const language = useContext(LanguageContext)[0];

  function createReference(Data) {
    return <Reference
      key={Data.id}
      logo={Data.logo}
      person={Data.person}
      date={Data.date[language]}
      reference={Data.reference[language]}
      />;
  }

  return (
    <div className="courses-container container-fluid" id="references">
      <div className="row">
        <div className="title-section">{TitleData[language][6].toUpperCase()}</div>
      </div>
      {ReferenceData.map(createReference)}
    </div>
  );
}
