import React, { useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import TitleData from "../data/TitleData";

import LightBoxCourses from "./LightBoxCourses";
import Course from "./Course";
import CoursesData from "../data/CoursesData";

import british_logo from "../images/Other/uk_flag.png";
import british_international_school from "../images/Certifications/british_international_school.png";
import meta from "../images/Analytics/meta.png";
import alteryx from "../images/Analytics/alteryx.png";
import td from "../images/Analytics/td.png";
import google from "../images/Analytics/google.png";
import meta_marketing_science from "../images/Certifications/meta_marketing_science.png";
import alteryx_core from "../images/Certifications/alteryx_core.png";
import alteryx_advanced from "../images/Certifications/alteryx_advanced.png";
import td_beginner from "../images/Certifications/td_beginner.png";
import td_intermediate from "../images/Certifications/td_intermediate.png";
import td_advanced from "../images/Certifications/td_advanced.png";
import td_insights from "../images/Certifications/td_insights.png";
import td_professional from "../images/Certifications/td_professional.png";
import ga_beginner from "../images/Certifications/ga_beginner.png";
import ga_advanced from "../images/Certifications/ga_advanced.png";
import google_tag_manager from "../images/Certifications/google_tag_manager.png";


export default function Courses() {
  const language = useContext(LanguageContext)[0];

  function createCourse(Data) {
    return <Course key={Data.id} logo={Data.logo} title={Data.title} />;
  }

  return (
    <div className="courses-container container-fluid" id="courses">
      <div className="row">
        <div className="title-section">{TitleData[language][4].toUpperCase()}</div>
      </div>

      <div className="row course">
        <div className="course-img-div">
          <img src={british_logo} alt="course-img" className="course-img" />
        </div>
        <div className="course-title">
          <div className="row row-course-certification">
            British International School &ensp;
            <LightBoxCourses certificate={british_international_school} reference="cert1" />
          </div>
        </div>
      </div>

      <div className="row course">
        <div className="course-img-div">
          <img src={meta} alt="course-img" className="course-img" />
        </div>
        <div className="course-title">
          <div className="row row-course-certification">
            Meta Marketing Science Professional &ensp;
            <LightBoxCourses certificate={meta_marketing_science} reference="cert1" />
          </div>
        </div>
      </div>

      <div className="row course">
        <div className="course-img-div">
          <img src={alteryx} alt="course-img" className="course-img" />
        </div>
        <div className="course-title">
          <div className="row row-course-certification">
            alteryx Designer &ensp;
            <LightBoxCourses certificate={alteryx_core} reference="cert1" />
            <LightBoxCourses certificate={alteryx_advanced} reference="cert2" />
          </div>
        </div>
      </div>

      <div className="row course">
        <div className="course-img-div">
          <img src={td} alt="course-img" className="course-img" />
        </div>
        <div className="course-title">
          <div className="row row-course-certification">
            Treasure Data &ensp;
            <LightBoxCourses certificate={td_beginner} reference="cert3" />
            <LightBoxCourses certificate={td_intermediate} reference="cert4" />
            <LightBoxCourses certificate={td_advanced} reference="cert5" />
            <LightBoxCourses certificate={td_insights} reference="cert6" />
            <LightBoxCourses certificate={td_professional} reference="cert7" />
          </div>
        </div>
      </div>

      <div className="row course">
        <div className="course-img-div">
          <img src={google} alt="course-img" className="course-img" />
        </div>
        <div className="course-title">
          <div className="row row-course-certification">
            Google Analytics and Tag Manager &ensp;
            <LightBoxCourses certificate={ga_beginner} reference="cert8" />
            <LightBoxCourses certificate={ga_advanced} reference="cert9" />
            <LightBoxCourses certificate={google_tag_manager} reference="cert10" />
          </div>
        </div>
      </div>

      {CoursesData.map(createCourse)}
    </div>
  );
}
