import React, { useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import UniversityProgramData from "../data/UniversityProgramData";
import dissertation from "../University/dissertation.pdf";
import certificate_of_degree from "../University/certificate_of_degree.pdf";

export default function AIMLProgram(props) {

  const language = useContext(LanguageContext)[0];

  return (
    <div id="accordion" style={{ display: props.disp }}>
      <div className="card">
        <div className="card-header col-sm-12" id="headingOne">
          <h5 className="mb-0">
            <button
              className="btn btn-link"
              data-toggle="collapse"
              data-target="#collapseOne"
              aria-expanded="true"
              aria-controls="collapseOne"
            >
              {UniversityProgramData["ME"]["titles"][0].toUpperCase()}
            </button>
          </h5>
        </div>

        <div
          id="collapseOne"
          className="collapse show"
          aria-labelledby="headingOne"
          data-parent="#accordion"
        >
          <div className="card-body">
          <ul className="bullet-point-alpha">
          <p>
            <div className="work-experience-date">
            <a
              href={language === "UK" ? certificate_of_degree : certificate_of_degree} download={language === "UK" ? "Diego Bettega - Certificate of Degree" : "Diego Bettega - Certificato di Laurea"}
              className="btn shadow-none btn-outline-dark btn-sm btn-work-experience"
            >
              {language === "UK" ? "Download Certificate of Degree" : "Scarica Certificato di laurea"}
            </a>
            &nbsp;&nbsp;
            <a
              href={language === "UK" ? dissertation : dissertation} download={language === "UK" ? "Diego Bettega - Dissertation" : "Diego Bettega - Tesi"}
              className="btn shadow-none btn-outline-dark btn-sm btn-work-experience"
            >
              {language === "UK" ? "Download Dissertation" : "Scarica Tesi"}
            </a>
            </div>
          </p>
            <li>{UniversityProgramData["ME"]["body1"][0]}</li>
            <li>{UniversityProgramData["ME"]["body1"][1]}</li>
            <li>{UniversityProgramData["ME"]["body1"][2]}</li>
            <li>{UniversityProgramData["ME"]["body1"][3]}</li>
            <li>{UniversityProgramData["ME"]["body1"][4]}</li>
            <li>{UniversityProgramData["ME"]["body1"][5]}</li>
            <li>{UniversityProgramData["ME"]["body1"][6]}</li>
            <li>{UniversityProgramData["ME"]["body1"][7]}</li>
            <li>{UniversityProgramData["ME"]["body1"][8]}</li>
            <li>{UniversityProgramData["ME"]["body1"][9]}</li>
            <li>{UniversityProgramData["ME"]["body1"][10]}</li>
            <li>{UniversityProgramData["ME"]["body1"][11]}</li>
            <li>{UniversityProgramData["ME"]["body1"][12]}</li>
            <li>{UniversityProgramData["ME"]["body1"][13]}</li>
            <li>{UniversityProgramData["ME"]["body1"][14]}</li>
            <li>{UniversityProgramData["ME"]["body1"][15]}</li>
            <li>{UniversityProgramData["ME"]["body1"][16]}</li>
            <li>{UniversityProgramData["ME"]["body1"][17]}</li>
            <li>{UniversityProgramData["ME"]["body1"][18]}</li>
            <li>{UniversityProgramData["ME"]["body1"][19]}</li>
            <li>{UniversityProgramData["ME"]["body1"][20]}</li>
          </ul>
          </div>
        </div>
      </div>

    </div>
  );
}
