import React, { useState, useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import WorkExperienceData from "../data/WorkExperienceData";
import MessageBtnData from "../data/MessageBtnData";
import rubiks_presentation from "../Rubiks/Presentation.pdf";


export default function Personal() {
  const language = useContext(LanguageContext)[0];
  const [visPersonal1, setVisPersonal1] = useState("none");
  const [visPersonal2, setVisPersonal2] = useState("");
  const [isMousedOver, setMouseOver] = useState(false);

  var [message, setMessage] = useState(0);

  function handleClickPersonal() {
    if (visPersonal1 === "none") {
      setVisPersonal1("");
      setVisPersonal2("none");
      setMessage(1);
    } else {
      setVisPersonal1("none");
      setVisPersonal2("");
      setMessage(0);
    }

    function handleMouseOver() {
      setMouseOver(true);
    }

    function handleMouseOut() {
      setMouseOver(false);
    }


  }





  return (
    <div className="timeline">
      <div className="timeline-content">
        <div className="timeline-icon">
          <img
            src={WorkExperienceData["personal"]["icon"]}
            alt="uni-img"
            className="company-icon centered"
          />
        </div>
        <h3 className="title">{WorkExperienceData["personal"]["title"]}</h3>
        <div className="description">
          <div className="row company-description">
            {WorkExperienceData["personal"]["name"][language]}&nbsp;
            <div className="tooltips tooltips-workexperience">
              <i className="fa fa-info-circle"></i>
              <span className="tooltiptext">
                {WorkExperienceData["personal"]["description"][language]}
              </span>
            </div>
          </div>
          <p>
            <div className="work-experience-date">
              <a className="rubiks" href="https://lucid.app/lucidchart/2460081c-490f-4bf6-86ae-0fae1520d70e/view?page=0_0#">
                {WorkExperienceData["personal"]["dataflow"][language]}
              </a>
            </div>
          </p>
          <p>
            <div className="work-experience-date">
            <a
              href={language === "UK" ? rubiks_presentation : rubiks_presentation} download={language === "UK" ? "Diego Bettega - Rubiks Presentation" : "Diego Bettega - Rubiks Presentazione"}
              className="btn shadow-none btn-outline-dark btn-sm btn-work-experience"
            >
              {language === "UK" ? "Download Presentation" : "Scarica Presentazione"}
            </a>
            </div>
          </p>
          <ul style={{ display: visPersonal2 }} className="bullet-point">
            <li className="work-experience-experience">
              {WorkExperienceData["personal"]["experience"][language][0]}
            </li>
            <li className="work-experience-experience">{"..."}</li>
          </ul>
          <ul style={{ display: visPersonal1 }} className="bullet-point">
            <li className="work-experience-experience">
              {WorkExperienceData["personal"]["experience"][language][0]}
            </li>
            <li className="work-experience-experience">
              {WorkExperienceData["personal"]["experience"][language][1]}
            </li>
            <li className="work-experience-experience">
              {WorkExperienceData["personal"]["experience"][language][2]}
            </li>
            <li className="work-experience-experience">
              {WorkExperienceData["personal"]["experience"][language][3]}
            </li>
          </ul>
          <div style={{marginLeft : 48}}>
            <a
              className="btn shadow-none btn-outline-dark btn-sm btn-work-experience"
              onClick={handleClickPersonal}
              data-toggle="collapse"
              href="#personal"
              role="button"
            >
            {MessageBtnData[language][message]}
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
