import React from "react";

export default function Reference(props) {
  return (
    <div className="row course">

      <div className="row course">

        <div className="course-img-div">
          <img src={props.logo} alt="reference-img" className="reference-img" />
        </div>

        <div className="reference-container">
          <div>
            <div className="reference-person ">{props.person}</div>
            <div className="reference-date ">{props.date}</div>
          </div>
        </div>


      </div>
    <div className="reference-reference">{props.reference}
    </div>
    </div>
  );
}
